/* appeal-alert ***************************************************************/

.content .appeal-alert {
  border: 4px solid #e40d0d;
  border-radius: 7px;
  margin-bottom: 32px;
  margin-top: 32px;
  font-family: "Lato", sans-serif;
}

.content .appeal-alert__heading {
  height: 42px;
  background: #e40d0d;
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: 800;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  margin: 0;
}

.content .appeal-alert__body {
  padding: 0 24px;
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}
.grid { display: flex; flex-wrap: wrap; margin: -1rem 0 0 -1rem; }

.grid .cell { flex: 1; padding: 1rem 0 0 1rem; }

.grid.small-fit > .cell { flex: 1; }

.grid.small-full > .cell { flex: 0 0 100%; }

@media (min-width: 48em) 
{
  .grid.large-fit > .cell { flex: 1; }

  .grid.large-full > .cell { flex: 0 0 100%; }
}
.skeleton 
{
  display: inline-block;
  height: 1em;
  position: relative;
  overflow: hidden;
  background-color: #e6e6e6;
  border-radius: 3px;
}

.skeleton::after 
{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translateX(-100%);
  background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.2) 20%, rgba(255, 255, 255, 0.5) 60%, rgba(255, 255, 255, 0));
  -webkit-animation: shimmer 3s infinite;
          animation: shimmer 3s infinite;
  content: "";
}

@-webkit-keyframes shimmer {
  100% {
    transform: translateX(100%);
  }
}

@keyframes shimmer {
  100% {
    transform: translateX(100%);
  }
}
.appeal-form {
